"use client";

import { useTranslations } from 'next-intl';
import { ArrowUpRight, Clock, GaugeCircle, Flag } from "lucide-react";
import { HypothesisStageSummary } from "@/lib/hypothesis-data";
import { CardHoverEffects } from "@/components/ui/card-hover-effects";

const healthBadge = {
  "on-track": "border-[rgba(0,255,136,0.55)] text-neon-green",
  warning: "border-[rgba(255,180,71,0.55)] text-[rgba(255,200,120,0.9)]",
  risk: "border-[rgba(255,59,241,0.55)] text-[rgba(255,120,210,0.9)]"
};

interface HypothesisLifecycleProps {
  stages: HypothesisStageSummary[];
}

export function HypothesisKanban({ stages }: HypothesisLifecycleProps) {
  const t = useTranslations('hypotheses.kanban');
  const tCommon = useTranslations('common');
  const totalHypotheses = stages.reduce((acc, stage) => acc + stage.items.length, 0);

  return (
    <section className="rounded-3xl border border-border/70 bg-surface/80 px-5 py-5">
      <div className="flex flex-col gap-2 md:flex-row md:items-center md:justify-between">
        <div>
          <p className="text-[11px] uppercase tracking-[0.32em] text-text-secondary/60">{t('title')}</p>
          <h2 className="mt-1.5 text-lg font-semibold text-text-primary lg:text-xl">{t('subtitle')}</h2>
          <p className="mt-2 text-sm text-text-secondary">
            {t('description')}
          </p>
        </div>
        <span className="inline-flex items-center gap-2 rounded-full border border-border/60 px-3 py-1.5 text-[11px] uppercase tracking-[0.24em] text-text-secondary">
          {t('activeHypotheses', { count: totalHypotheses })}
        </span>
      </div>

      <div className="mt-5 -mx-5 overflow-x-auto pb-4">
        <div className="flex gap-4 min-w-max px-5">
          {stages.map((stage) => {
            const gradientClasses = {
              "on-track": "from-[rgba(0,255,136,0.15)] to-[rgba(0,255,136,0.05)] border-[rgba(0,255,136,0.4)]",
              warning: "from-[rgba(255,180,71,0.15)] to-[rgba(255,180,71,0.05)] border-[rgba(255,180,71,0.4)]",
              risk: "from-[rgba(255,59,241,0.15)] to-[rgba(255,59,241,0.05)] border-[rgba(255,59,241,0.4)]",
            };

            return (
              <article
                key={stage.key}
                className={`flex h-full min-w-[300px] max-w-[300px] flex-col gap-3 rounded-2xl border bg-gradient-to-br p-4 transition duration-300 ${gradientClasses[stage.stageHealth]} hover:shadow-[0_0_28px_rgba(0,255,136,0.25)]`}
              >
                <header>
                  <div className="flex items-center justify-between">
                    <div className="flex-1">
                      <h3 className="text-base font-semibold text-text-primary">{stage.title}</h3>
                    </div>
                    <span
                      className={`ml-2 rounded-full border px-2.5 py-1 text-[11px] uppercase tracking-wide whitespace-nowrap ${healthBadge[stage.stageHealth]}`}
                    >
                      {t(`health.${stage.stageHealth}`)}
                    </span>
                  </div>
                  <p className="mt-2 text-xs leading-relaxed text-text-secondary">{stage.description}</p>
                </header>

                <dl className="grid grid-cols-3 gap-2 text-[11px] uppercase tracking-wide text-text-secondary">
                  <div className="flex items-center gap-1">
                    <Clock className="h-3.5 w-3.5 text-neon-green/70" />
                    <div>
                      <dt>{t('averageDays')}</dt>
                      <dd className="text-text-primary">{stage.averageDaysInStage}{tCommon('days')}</dd>
                    </div>
                  </div>
                  <div className="flex items-center gap-1">
                    <GaugeCircle className="h-3.5 w-3.5 text-neon-green/70" />
                    <div>
                      <dt>{t('conversion')}</dt>
                      <dd className="text-text-primary">{Math.round(stage.conversionRate * 100)}%</dd>
                    </div>
                  </div>
                  <div className="flex items-center gap-1">
                    <Flag className="h-3.5 w-3.5 text-neon-green/70" />
                    <div>
                      <dt>{t('sla')}</dt>
                      <dd className="text-text-primary">{stage.slaHours}{tCommon('hours')}</dd>
                    </div>
                  </div>
                </dl>

                <ul className="flex flex-1 flex-col gap-2 overflow-y-auto">
                  {stage.items.map((item) => (
                    <li key={item.id}>
                      <CardHoverEffects className="p-3">
                        <div className="flex items-center justify-between text-[11px] uppercase tracking-wide text-text-secondary/70">
                          <span>{item.id}</span>
                          <span>{item.lastUpdated}</span>
                        </div>
                        <p className="mt-1.5 text-sm font-semibold text-text-primary leading-tight">{item.title}</p>
                        <div className="flex items-center justify-between mt-1.5 text-xs text-text-secondary">
                          <span>{item.owner}</span>
                          <span className="flex items-center gap-1 text-neon-green">
                            <ArrowUpRight className="h-3 w-3" />
                            {item.nextGate.replace(/_/g, " ")}
                          </span>
                        </div>
                      </CardHoverEffects>
                    </li>
                  ))}
                  {stage.items.length === 0 && (
                    <li className="rounded-xl border border-dashed border-border/60 bg-surface-hover/50 p-6 text-center text-xs text-text-secondary/70">
                      {t('noHypotheses')}
                    </li>
                  )}
                </ul>
              </article>
            );
          })}
        </div>
      </div>
    </section>
  );
}

